<template><section><h2>Indicator 加载提示</h2>
<p>Indicator 采用单例模式，即同一时间只会存在一个 Indicator。</p>
<p>且当上一个Indicator 关闭时，另一个才会显示。</p>
<div class="demo-block mt_15">
  <tj-button round="3px" @click="handle">默认</tj-button>
  <tj-button round="3px" class="ml_10" @click="handle2">文字</tj-button>
  <tj-button round="3px" class="ml_10" @click="handle3">图标</tj-button>
  
</div>
<demo-block>
                    <div slot="desc"><tj-button round="3px" @click="handle">默认</tj-button>
<tj-button round="3px" class="ml_10" @click="handle2">文字</tj-button>
<tj-button round="3px" class="ml_10" @click="handle3">图标</tj-button>


</div>
                  <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">tj-button</span> <span class="hljs-attr">round</span>=<span class="hljs-string">"3px"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"handle"</span>&gt;</span>默认<span class="hljs-tag">&lt;/<span class="hljs-name">tj-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">tj-button</span> <span class="hljs-attr">round</span>=<span class="hljs-string">"3px"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"ml_10"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"handle2"</span>&gt;</span>文字<span class="hljs-tag">&lt;/<span class="hljs-name">tj-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">tj-button</span> <span class="hljs-attr">round</span>=<span class="hljs-string">"3px"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"ml_10"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"handle3"</span>&gt;</span>图标<span class="hljs-tag">&lt;/<span class="hljs-name">tj-button</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
      handle() {
        <span class="hljs-keyword">this</span>.$indicator.open();
        setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> <span class="hljs-keyword">this</span>.$indicator.close(), <span class="hljs-number">2000</span>);
      },
      handle2() {
        <span class="hljs-keyword">this</span>.$indicator.open(<span class="hljs-string">"加载中"</span>);
        setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> <span class="hljs-keyword">this</span>.$indicator.close(), <span class="hljs-number">2000</span>);
      },
      handle3() {
        <span class="hljs-keyword">this</span>.$indicator.open({
          <span class="hljs-attr">text</span>: <span class="hljs-string">"加载中"</span>,
          <span class="hljs-attr">iconType</span>: <span class="hljs-string">"circular"</span>
        });
        setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> <span class="hljs-keyword">this</span>.$indicator.close(), <span class="hljs-number">2000</span>);
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>text</td>
<td>文本内容</td>
<td>string</td>
<td></td>
<td></td>
</tr>
<tr>
<td>iconType</td>
<td>图标类型</td>
<td>string</td>
<td>spinner, circular</td>
<td>spinner</td>
</tr>
</tbody>
</table>
</section></template>

<script>
    export default {
      methods: {
        handle() {
          this.$indicator.open();
          setTimeout(() => this.$indicator.close(), 2000);
        },
        handle2() {
          this.$indicator.open("加载中");
          setTimeout(() => this.$indicator.close(), 2000);
        },
        handle3() {
          this.$indicator.open({
            text: "加载中",
            iconType: "circular"
          });
          setTimeout(() => this.$indicator.close(), 2000);
        }
      }
    }
  </script>


